<template>
  <div>
    <van-nav-bar title="登录" class="page-nav-bar">
          <van-icon slot="left" name="cross" @click="$router.back()"></van-icon>
    </van-nav-bar>
    <van-form @submit="onSubmit" ref="formRef">
      <van-field
        v-model="user.mobile"
        name="mobile"
        placeholder="请填写手机号"
        :rules="rules.mobile"
      >
        <!-- 具名插槽写法-2.6以后,完整写法 -->
        <!-- <template v-slot:left-icon>
          <i class="toutiao toutiao-shouji"></i>
        </template> -->
        <!-- 具名插槽写法-2.6以后,简化写法 -->
        <!-- <template #left-icon>
          <i class="toutiao toutiao-shouji"></i>
        </template> -->
        <!-- 具名插槽写法-2.6以前 -->
        <i slot="left-icon" class="toutiao toutiao-shouji"></i>
      </van-field>
      <van-field
        v-model="user.code"
        name="code"
        placeholder="请填写验证码"
        :rules="rules.code"
      >
        <i slot="left-icon" class="toutiao toutiao-yanzhengma"></i>
        <van-count-down
          v-if="isCountDownShow"
          slot="button"
          :time="1000 * 5"
          format="ss s秒"
          @finish="isCountDownShow = false"
        />
        <van-button
          slot="button"
          class="send-sms-btn"
          type="default"
          size="mini"
          round
          v-else
          native-type="button"
          @click="onSendCode"
          >获取验证码</van-button
        >
      </van-field>
      <div class="login-btn-wrap">
        <van-button class="login-btn" block type="info" native-type="submit"
          >登录</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
// import { login, sendSms } from '../../api/user.js'
import { login, sendSms } from '../../api/user.js'
export default {
  name: 'login_page',
  components: {},
  data () {
    return {
      username: '',
      password: '',
      user: {
        mobile: '13438950453',
        code: '246810'
      },
      rules: {
        mobile: [
          { required: true, message: '请填写手机号' },
          { pattern: /^1[3|5|7|8]\d{9}$/, message: '请填写正确的手机号' }
        ],
        code: [{ required: true, message: '请填写验证码' }, { pattern: /^\d{6}$/, message: '请填写正确的验证码' }]
      },
      isCountDownShow: false // 控制倒计时显示
    }
  },
  created () {},
  mounted () {},
  methods: {
    async onSendCode () {
      console.log('点击按钮')
      try {
        // 检验手机号是否正确
        // 通过ref得到组件实例
        this.$refs.formRef.validate('mobile')
        console.log('检验成功')
        // 发送请求
        await sendSms(this.user.mobile)
        // 显示倒计时
        this.isCountDownShow = true
      } catch (e) {
        // 2种错误
        // 错误1-手机号不正确,e={name:'mobile',message:'请填写正确的手机号'}
        // 错误2-连续点击,e={response:{status:429}}
        console.log('检验失败')
        console.log(e)
        if (e.response && e.response.status === 429) {
          this.$toast('请稍后重试')
        } else {
          this.$toast('手机号不正确')
        }
      }
    },

    async onSubmit (values) {
      // 通过检验才会触发 submint
      try {
        console.log('submit', values)
        const res = await login(values)
        console.log(res)
        this.$store.commit('setToken', res.data.data.token)
        this.$toast.success('登录成功')
        // this.$router.push('/my')
      } catch (e) {
        console.log(e)
        if (e.response.status === 400) {
          this.$toast.fail('登录失败')
        }
      }
    }

  }
}
</script>

<style lang="less" scoped>
.login-container {
  .toutiao {
    font-size: 37px;
  }
  .send-sms-btn {
    width: 152px;
    height: 46px;
    line-height: 46px;
    background-color: #ededed;
    font-size: 22px;
    color: #666;
  }
  .login-btn-wrap {
    padding: 53px 33px;
    .login-btn {
      background-color: #6db4fb;
      border: none;
    }
  }
}
</style>
